body {
  margin: 0;
}

.slider-slick-main {
  outline: none;
}

.button__bar {
  display: inline-block;
  vertical-align: middle;
  margin: auto;
  padding: 0;
  border-radius: 40px;
  text-align: center;
}

.button__bar li {
  list-style: none;
  cursor: pointer;
  display: inline-block;
  margin: 0 3px;
}

.button__bar li button {
  border: none;
  background: #ab967f;
  color: transparent;
  cursor: pointer;
  display: block;
  height: 10px;
  width: 10px;
  border-radius: 5px;
}

.button__bar li.slick-active button {
  width: 30px;
  background-color: #ff9800;
}

.button__bar li button:hover,
.button__bar li button:focus {
  background: #ff9800;
  outline: 0;
}

.MuiAutocomplete-option {
  padding: 0 !important;
}
